<template>
  <div class="organization-select">
    <el-cascader :placeholder="holder" expand-trigger="hover" :options="treeList" :change-on-select="true" v-model="selectedOptions" :props=treeProps :clearable="true" :show-all-levels="false" @change="handleChange" ref="refHandle">
    </el-cascader>
  </div>
</template>
<script>
import bus from '../../common/bus';
export default {
  name: 'AreaTreeSelect', //区域树形下拉框
  props: [
    'defaultOptions',//默认选中项id
    'title',//标题
    //'disabled',//	是否启用
  ],
  data () {
    return {
      selectedOptions: [],//选中值--必须是完整路径eg:['节点code'] 或 ['父节点code','子节点code']
      treeProps: {
        value: 'areaId',
        label: 'areaName',
        children: 'children',
        checkStrictly: true
      },
      oldTreeList: [],//原始数据
      treeList: [],//必须是树形结构的对象数组
      holder: "请选择",
      findObject: {},
    }
  },
  created () {
    if (this.title) this.holder = this.title;
    this.getData();
    // if(this.disabled === undefined) this.disabled = false;
  },
  mounted () {

  },
  watch: {
    findObject () {
      if (this.$refs.refHandle) {
        this.$refs.refHandle.dropDownVisible = false; //监听值发生变化就关闭它
      }
    }
  },
  methods: {

    /**
     * 监听change事件
     * 回调父组件中OrgTreeSelectCall事件，传回选中的value值
     * @param {Array} valArray 选中的值
     */
    handleChange (valArray) {
      let returnId = '';
      let findId = '';
      if (valArray && valArray.length > 0) {
        findId = valArray[valArray.length - 1];
      }
      this.findObjById(this.treeList, findId);
      this.$emit('areaTreeSelectCall', this.findObject);
    },

    /**
     * 按照id查找数据
     * @param {objectArr} 循环数据
     * @param {id} 需要查找的id
     */
    findObjById (objectArr, id) {
      for (let i = 0; i < objectArr.length; i++) {
        if (id == objectArr[i].areaId) {
          this.findObject = objectArr[i];
          return;
        }
        if (objectArr[i].children != undefined && objectArr[i].children.length != 0) {
          this.findObjById(objectArr[i].children, id);
        }
      }
    },
    /**
     * 获取列表数据
     */
    getData () {
      var self = this;
      self.$http({
        url: self.$http.adornUrl(self.API.getAreaTreeArea),
        method: "post",
        data: self.$http.adornData({})
      }).then(({ data }) => {
        if (data.code == "0") {
          //递归转换
          self.treeList = self.commonFun.getTreeData(data.data);
          let returnObj = {};
          if (self.defaultOptions) {
            //self.selectedOptions = self.commonFun.createArrIdAndReverse(self.treeList,"areaId");
            self.selectedOptions = self.defaultOptions;
            returnObj = self.findObjById(self.treeList, self.defaultOptions);
            self.$emit('areaTreeSelectCall', returnObj);
          } else {
            self.selectedOptions.push(self.treeList[0].areaId);
            // returnObj = self.treeList[self.treeList.length-1];
            returnObj = self.treeList[0];
            self.$emit('areaTreeSelectCall', returnObj);
          }
        } else {
          self.$message({
            type: 'error',
            message: '获取区域树形下拉框数据失败，请刷新重试！'
          });
        }
      }).catch((err) => {
        console.log(err)

        self.$message({
          type: 'error',
          message: '请求异常，请检查网络！'
        });
      })

    }
  },
};
</script>
<style lang="scss" scoped type="text/css">
.el-scrollbar .el-scrollbar__view .el-select-dropdown__item {
  height: auto;
  padding: 0;
}

.el-select-dropdown__item.selected {
  font-weight: normal;
}

ul li >>> .el-tree .el-tree-node__content {
  height: auto;
}

.el-tree-node__label {
  font-weight: normal;
}

.el-tree >>> .is-current .el-tree-node__label {
  color: #409eff;
  font-weight: 700;
}

.el-tree >>> .is-current .el-tree-node__children .el-tree-node__label {
  color: #606266;
  font-weight: normal;
}
</style>



