<template>
  <div class="site-page--login">
    <vue-particles
      color="#fff"
      :particleOpacity="0.7"
      :particlesNumber="60"
      shapeType="circle"
      :particleSize="3"
      linesColor="#fff"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="6"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push"
    ></vue-particles>
    <div class="login-main">
      <div class="login-title">组工一体化平台</div>
      <div class="contentLogin">
            <div class="leftLogin">
        			<img src="../../assets/img/Head_portrait01.png" alt="" />
        		</div>
        		<div class="rightLogin">
        			<div class="line"></div>
                <el-form :model="loginForm" label-width="85px" :rules="loginRules" ref="loginForm" @keyup.enter.native="loginFormSubmit()" status-icon>
                  <el-form-item label="区域"  prop="uuid">
                    <areaTreeSelect :defaultOptions="loginForm.uuid" @areaTreeSelectCall = "getSelect"></areaTreeSelect>
                  </el-form-item>
                  <el-form-item label="账号" prop="userName"><el-input v-model="loginForm.userName" placeholder="帐号"></el-input></el-form-item>
                  <el-form-item label="密码" prop="password"><el-input v-model="loginForm.password" type="password" placeholder="密码"></el-input></el-form-item>
                  <el-form-item><el-button class="login-btn-submit" type="primary" @click="loginFormSubmit()">登录</el-button></el-form-item>
                </el-form>
            </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { getUUID } from '@/utils';
  import areaTreeSelect from '../../components/baseComponents/AreaTreeSelect.vue';
  export default {
    name: "Login",
    props: [""],
    components: {
			areaTreeSelect
		},
    data() {
      let defaultAreaId = localStorage.getItem(this.API.constObj.loginAreaCode);
        	if(defaultAreaId == undefined || defaultAreaId.length == 0){
        		defaultAreaId = undefined;
        }
      return {
        loginForm: {
          userName: '',
          password: '',
          uuid: ''
        },
        loginRules: {
          //uuid: [{ required: true, message: '请选择区域', trigger: 'blur' }],
          userName: [{ required: true, message: '帐号不能为空', trigger: 'blur' }],
          password: [{ required: true, message: '密码不能为空', trigger: 'blur' }]
        }
      };
    },
    methods: {
      loginFormSubmit() {
        let _this = this
        //提交表单
        _this.$refs['loginForm'].validate(valid => {
          if (valid) {
            _this.$http({
              url: _this.$http.adornUrl(_this.API.login),
              method: 'post',
              data: _this.$http.adornData({
                username: _this.loginForm.userName,
                password: _this.loginForm.password,
                //areacode: "130000"
                areaId: _this.loginForm.uuid
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                sessionStorage.setItem('token', JSON.stringify(data.data.token));
                this.$router.push('/home')
                // _this.$router.replace({ name: 'home' });
              } else {
                _this.$message.error(data.msg);
              }
            });
          }
        });
      },
      /*
      * 区域机构树形下拉框组件回调方法--子组件回传数据的方法
      * @param {Object} returnObj 选中的数据对象
      */
      getSelect(returnObj){
        let storeObject = {
          name:this.API.constObj.loginAreaCode,
          storeInfo:""
        };
        if(returnObj && returnObj.areaCode){
          this.loginForm.uuid = returnObj.areaId;
          storeObject.storeInfo = returnObj.areaCode;
          console.log(this.loginForm);
        }else{
          this.loginForm.uuid = "";
          storeObject.storeInfo = "";
        }
				localStorage.setItem(this.API.constObj.loginAreaCode, storeObject.storeInfo);
      },
    },
    created() {
    }
  };
</script>

<style lang="scss">
  .site-page--login {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(~@/assets/img/login_bg.png) no-repeat;
    background-size: 100% 100%;
    .login-main {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width:500px;
      height:329px;
      background:rgba(255,255,255,0.9);
      box-shadow:0px 27px 69px 0px rgba(0,18,96,0.13);
      border-radius:8px;
      text-align: center;
      .login-title{
        text-align: center;
        font-size: 28px;
        font-weight: 500;
        height: 60px;
        line-height: 60px;
        background-color: #4488c0;
        color: #fff;
      }
      .contentLogin{
            display: flex;
            justify-content: space-between;
            border-top: 1px solid #BABDD8;
	        	.leftLogin{
	        		img{
	        			margin-top:58px;
	        			margin-left: 38px;
	        			border: 1px solid #2B92C2;
	        			padding: 20px;
	        			width:100px;
	        			height:100px;
	        		}
            }
            .rightLogin{
                font-size: 12px;
                margin-top: 20px;
                display: flex;
                text-align: center;
                width: 65%;
                .line{
                  width: 1px ;
                  height: 200px;
                  background-color:#A6A9AD;
                }
               .el-form{
                  .login-btn-submit {
                    width: 100%;
                    margin-top: 10px;
                  }
                }
            }
      }

    }
  }
</style>



